    border-radius:15px!important;
  }
  .card-placement-bottom{
    border: 1px solid #fff;
    border-radius: 12px!important;
  }
  .nav.nav-tabs .nav-item .nav-link.active {
    border: none;
    position: relative;
    color: #FC2B5A;
    transition: all .2sease;
    background-color: transparent;
}
.nav.nav-tabs .nav-item .nav-link {
    color: #626262;
    font-size: 0.95rem;
    border: none;
    min-width: auto;
    font-weight: 450;
    padding: .61rem .635rem;
    border-radius: 0;
}
.btn-primary {
    color: #fff;
    background-color: #FC2B5A!important;
    border-color: #FC2B5A;
}
  /*
  * Col 3 layout for detached and general type
  */
  @media (min-width: 992px) {
    body .content-right {
      width: calc(100vw - (100vw - 100%) - 230px);
      float: right;
    }
  
    body .content-left {
      width: calc(100vw - (100vw - 100%) - 230px);
      float: left;
    }
  
    body .content-detached {
      width: 100%;
    }
  
    body .content-detached.content-right {
      float: right;
      margin-left: -230px;
    }
  
    body .content-detached.content-right .content-body {
      margin-left: calc(230px + 2.2rem);
    }
  
    body .content-detached.content-left {
      float: left;
      margin-right: -230px;
    }
  
    body .content-detached.content-left .content-body {
      margin-right: calc(230px + 2.2rem);
    }
  
    .sidebar-right.sidebar-sticky {
      float: right !important;
      margin-left: -230px;
      width: 230px !important;
      margin-top: 6rem;
    }
  }
  
  .truncate {
    overflow: hidden !important;
    text-overflow: ellipsis !important;
    white-space: nowrap !important;
  }
  
  @media (max-width: 575.98px) {
    html body .content .content-wrapper {
      padding: calc(2.2rem - 0.4rem) calc(2.2rem - 1rem) 0;
    }
  }
  
  @media (max-width: 575.98px) {
  
    body.navbar-static .app-content .header-navbar .navbar-container,
    body.navbar-sticky .app-content .header-navbar .navbar-container {
      padding-left: 1rem;
      padding-right: .5rem;
    }
  }
  
  @media screen and (-ms-high-contrast: active),
  (-ms-high-contrast: none) {
    html.full-screen {
      width: 100%;
    }
  
    html.full-screen .content.app-content {
      height: 100%;
      overflow: scroll;
      overflow-x: hidden;
    }
  }
  
