    padding: 0.175rem 0.5rem;
    font-size: 0.725rem;
    line-height: 1.677777;
    border-bottom-left-radius: 0.175rem;
    border-top-left-radius: 0.175rem;
  }
  
  .input-group-xs .input-group-btn>button {
    border-radius: 0.175rem;
